.club {
  margin-top: 128px;

  @include vp-1439 {
    margin-top: 42px;
  }
}

.club__title {
  margin: 0;
  color: $color-default;
  font-size: 56px;
  font-weight: 900;
  line-height: 100%;
  letter-spacing: -1.12px;
  max-width: 827px;

  @include vp-1439 {
    font-size: 32px;
    line-height: 32px;
  }
}

.club__svg {
  display: block;

  @include vp-1439 {
    flex:0 0 83px;
  }
}

.club__list {
  margin: 0;
  padding: 0;
  margin-top: 48px;
  display: grid;
  list-style: none;
  grid-template-columns: 1fr 1fr;
  row-gap:61px;

  @include vp-1439 {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 27px;
    margin-top: 32px;
  }
}

.club__item {
  color: $color-default;
  font-size: 24px;
  font-weight: 500;
  line-height: 120%;
  max-width: 400px;

  @include vp-1439 {
    display: flex;
    align-items: center;
    font-size: 16px;
    line-height: 19px;
    gap: 16px;
  }
}

.club__description {
  display: block;
  margin-top: 16px;

  @include vp-1439 {
    margin-top: 0;
  }
}
