.buy {
  position: relative;
  margin-top: 128px;
  max-width: 1440px;
  margin: 0 auto;
  margin-top: 148px;

  @include vp-1439 {
    margin-top: 72px;
    max-width: 375px;
    padding-bottom: 380px;
  }
}

.buy__img {
  display: block;
  position: absolute;
  top: -40%;
  left: 45%;
  object-position: 50% 50%;
  object-fit: contain;
  @include vp-1439 {
    display: none;
  }
}

.buy__title {
  margin: 0;
  color: $color-default;
  font-size: 48px;
  font-style: normal;
  font-weight: 900;
  line-height: 100%;
  letter-spacing: -0.96px;
  max-width: 407px;

  @include vp-1439 {
    font-size: 32px;
    line-height: 32px;
  }
}

.buy__description {
  margin: 0;padding: 0;
  margin-top: 48px;
  color: $color-default;
  font-size: 24px;
  font-style: normal;
  font-weight: 500;
  line-height: 120%;
  max-width: 419px;

  @include vp-1439 {
    margin-top: 32px;
    font-size: 16px;
    line-height: 19px;
  }
}
