.promo {
  position: relative;
}

.promo__wrapper {
  background-image: url("../img/bg-promo.png");
  background-repeat: no-repeat;
  background-size: contain;
  background-position: 380px 0;
  max-width: 1440px;
  padding: 125px 0 0 0;
  margin: 0 auto;
  width: 100%;
}
@media (max-width: 1439px) {
  .promo__wrapper {
    padding-top: 25px;
    padding-bottom: 325px;
    background-position: 0 100%;
    max-width: 375px;
    background-image: url("../img/bg-promo-mobile.png");
  }
}

.promo::after {
  position: absolute;
  background: linear-gradient(180deg, rgba(255, 255, 255, 0) 0%, #FFF 100%);
  content: "";
  width: 100%;
  min-height: 100%;
  top: 0;
  left: 0;
  z-index: -2;
}

.promo::before {
  position: absolute;
  background: linear-gradient(0deg, rgba(255, 255, 255, 0.2) 0%, rgba(255, 255, 255, 0.2) 100%), linear-gradient(90deg, #FF5C4D 0%, #EB469F 25%, #8341EF 72%, #3F68F9 100%);
  opacity: 0.22;
  content: "";
  width: 100%;
  min-height: 100%;
  top: 0;
  left: 0;
  z-index: -3;
}

.promo__title {
  margin-top: 72px;
  position: relative;
  color: #21201F;
  font-size: 72px;
  font-weight: 900;
  line-height: 100%;
  letter-spacing: -1.44px;
  max-width: 420px;
}
@media (max-width: 1439px) {
  .promo__title {
    font-size: 36px;
    line-height: 36px;
  }
}

.promo__description {
  color: #21201F;
  font-size: 24px;
  font-weight: 500;
  line-height: 130%;
  max-width: 435px;
  margin: 0;
  margin-top: 48px;
}
@media (max-width: 1439px) {
  .promo__description {
    font-size: 18px;
    line-height: 23px;
  }
}

@media (max-width: 1439px) {
  .promo__button-box {
    position: fixed;
    display: flex;
    align-items: flex-end;
    justify-content: center;
    bottom: 24px;
    left: 0;
    width: 100%;
    height: 49px;
    z-index: 4;
  }
}

.promo__button {
  margin-top: 44px;
  text-align: right;
  font-size: 24px;
  font-weight: 500;
  line-height: 22px;
}
@media (max-width: 1439px) {
  .promo__button {
    font-size: 18px;
    line-height: 22px;
    padding: 18px 75px;
  }
}

.promo__price {
  position: relative;
  padding: 2px 12px;
  border-radius: 500px;
  background: radial-gradient(141.42% 141.42% at 100% 0%, #E0B8FF 0%, #B6B9FF 100%);
  line-height: 29px;
}
@media (max-width: 1439px) {
  .promo__price {
    padding: 0 7px;
    font-size: 18px;
    line-height: 22px;
  }
}

.promo__price::after {
  font-weight: 500;
  position: absolute;
  z-index: -1;
  padding: 6px 12px 7px 27px;
  font-size: 16px;
  line-height: 120%;
  color: #808080;
  top: 0;
  left: 65px;
  content: "3190₽";
  text-decoration: line-through;
  border-radius: 0 500px 500px 0;
  background: linear-gradient(0deg, rgba(255, 255, 255, 0.63) 0%, rgba(255, 255, 255, 0.63) 100%), radial-gradient(141.42% 141.42% at 100% 0%, #E0B8FF 0%, #B6B9FF 100%);
}
@media (max-width: 1439px) {
  .promo__price::after {
    font-size: 13px;
    line-height: 120%;
    top: 0;
    left: 35px;
    padding: 3px 8px 3px 25px;
  }
}

.abonement {
  max-width: 1440px;
  margin: 0 auto;
  position: relative;
  margin-top: 128px;
}
@media (max-width: 1439px) {
  .abonement {
    margin: 0 auto;
    max-width: 375px;
  }
}

.abonement__box {
  margin-left: 550px;
}
@media (max-width: 1439px) {
  .abonement__box {
    margin: 0;
  }
}

.abonement__title {
  padding: 0;
  margin: 0;
  color: #21201F;
  font-size: 48px;
  font-weight: 900;
  line-height: 100%;
  letter-spacing: -0.96px;
  max-width: 407px;
}
@media (max-width: 1439px) {
  .abonement__title {
    font-size: 32px;
    line-height: 32px;
  }
}

.abonement__description {
  padding: 0;
  margin: 0;
  margin-top: 48px;
  color: #21201F;
  font-size: 24px;
  font-weight: 500;
  line-height: 120%;
  min-width: 419px;
}
@media (max-width: 1439px) {
  .abonement__description {
    margin-top: 32px;
    font-size: 16px;
    line-height: 24px;
  }
}

.abonement__img {
  position: absolute;
  left: 0;
  top: -100px;
}
@media (max-width: 1439px) {
  .abonement__img {
    top: -380px;
  }
}

.abonement__button {
  margin: 0;
  margin-top: 48px;
  color: #21201F;
  text-align: right;
  font-size: 24px;
  font-weight: 500;
  line-height: 22px;
  padding: 24px 48px;
}
@media (max-width: 1439px) {
  .abonement__button {
    margin-top: 32px;
    font-size: 18px;
    padding: 18px 75px;
  }
}

.buy {
  position: relative;
  margin-top: 128px;
  max-width: 1440px;
  margin: 0 auto;
  margin-top: 148px;
}
@media (max-width: 1439px) {
  .buy {
    margin-top: 72px;
    max-width: 375px;
    padding-bottom: 380px;
  }
}

.buy__img {
  display: block;
  position: absolute;
  top: -40%;
  left: 45%;
  object-position: 50% 50%;
  object-fit: contain;
}
@media (max-width: 1439px) {
  .buy__img {
    display: none;
  }
}

.buy__title {
  margin: 0;
  color: #21201F;
  font-size: 48px;
  font-style: normal;
  font-weight: 900;
  line-height: 100%;
  letter-spacing: -0.96px;
  max-width: 407px;
}
@media (max-width: 1439px) {
  .buy__title {
    font-size: 32px;
    line-height: 32px;
  }
}

.buy__description {
  margin: 0;
  padding: 0;
  margin-top: 48px;
  color: #21201F;
  font-size: 24px;
  font-style: normal;
  font-weight: 500;
  line-height: 120%;
  max-width: 419px;
}
@media (max-width: 1439px) {
  .buy__description {
    margin-top: 32px;
    font-size: 16px;
    line-height: 19px;
  }
}

.club {
  margin-top: 128px;
}
@media (max-width: 1439px) {
  .club {
    margin-top: 42px;
  }
}

.club__title {
  margin: 0;
  color: #21201F;
  font-size: 56px;
  font-weight: 900;
  line-height: 100%;
  letter-spacing: -1.12px;
  max-width: 827px;
}
@media (max-width: 1439px) {
  .club__title {
    font-size: 32px;
    line-height: 32px;
  }
}

.club__svg {
  display: block;
}
@media (max-width: 1439px) {
  .club__svg {
    flex: 0 0 83px;
  }
}

.club__list {
  margin: 0;
  padding: 0;
  margin-top: 48px;
  display: grid;
  list-style: none;
  grid-template-columns: 1fr 1fr;
  row-gap: 61px;
}
@media (max-width: 1439px) {
  .club__list {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 27px;
    margin-top: 32px;
  }
}

.club__item {
  color: #21201F;
  font-size: 24px;
  font-weight: 500;
  line-height: 120%;
  max-width: 400px;
}
@media (max-width: 1439px) {
  .club__item {
    display: flex;
    align-items: center;
    font-size: 16px;
    line-height: 19px;
    gap: 16px;
  }
}

.club__description {
  display: block;
  margin-top: 16px;
}
@media (max-width: 1439px) {
  .club__description {
    margin-top: 0;
  }
}

.exclusive {
  padding-bottom: 1000px;
  background-image: url("../img/exclusive.png");
  background-repeat: no-repeat;
  background-position: 0 50px;
  background-size: cover;
  max-width: 1440px;
  margin: 0 auto;
  margin-top: 128px;
}
@media (max-width: 1439px) {
  .exclusive {
    margin-top: 72px;
    background-image: none;
    padding-bottom: 0;
  }
}

.exclusive__title {
  margin: 0;
  max-width: 650px;
  color: #21201F;
  font-size: 48px;
  font-weight: 900;
  line-height: 100%;
  letter-spacing: -0.96px;
}
@media (max-width: 1439px) {
  .exclusive__title {
    font-size: 32px;
    line-height: 32px;
  }
}

.exclusive__description {
  position: relative;
  margin: 0;
  margin-top: 48px;
  color: #21201F;
  font-size: 24px;
  font-style: normal;
  font-weight: 500;
  line-height: 120%;
  max-width: 442px;
}
@media (max-width: 1439px) {
  .exclusive__description {
    font-size: 16px;
    line-height: 19px;
  }
}

@media (max-width: 1439px) {
  .exclusive__img {
    border-radius: 44px;
  }
}

.exclusive__swiper {
  display: none;
}
@media (max-width: 1439px) {
  .exclusive__swiper {
    display: block;
    position: relative;
    margin-top: 47px;
  }
}

.exclusive__swiper-decoration {
  position: absolute;
  object-fit: cover;
  z-index: 5;
  top: -74px;
  right: -17px;
}

.swiper-button-next::after {
  background-image: url("../img/svg/arrow-next.svg");
  width: 48px;
  height: 48px;
}

.swiper-button-prev::after {
  background-image: url("../img/svg/arrow-prew.svg");
  width: 48px;
  height: 48px;
}

.exclusive__img-next {
  position: relative !important;
  margin-top: 0 !important;
  width: 48px !important;
  height: 48px !important;
  order: 1;
}

.exclusive__img-prev {
  position: relative !important;
  margin-top: 0 !important;
  width: 48px !important;
  height: 48px !important;
}

.exclusive__img-next::after {
  content: "" !important;
}

.exclusive__img-prev::after {
  content: "" !important;
}

.exclusive__box {
  margin-top: 24px;
  display: flex;
  justify-content: center;
  gap: 36px;
}

