.btn {
  display: inline-flex;
  padding: 24px 48px;

  color: $color-default;

  background-color: $color-bg;
  border: none;
  border-radius: 36px;
  box-shadow: 0px 4px 98px 0px rgba(255, 222, 64, 0.59);
  cursor: pointer;

  transition: opacity $trans-default;

  &:disabled {
    opacity: 0.5;

    pointer-events: none;
  }

  @include hover-focus {
    opacity: 0.8;
  }
}
