.abonement {
  max-width: 1440px;
  margin: 0 auto;
  position: relative;
  margin-top: 128px;

  @include vp-1439 {
    margin: 0 auto;
    max-width: 375px;
  }
}

.abonement__box {
  margin-left: 550px;

  @include vp-1439 {
    margin: 0;
  }
}

.abonement__title {
  padding: 0;margin: 0;
  color: $color-default;
  font-size: 48px;
  font-weight: 900;
  line-height: 100%;
  letter-spacing: -0.96px ;
  max-width: 407px;

  @include vp-1439 {
    font-size: 32px;
    line-height: 32px;
  }
}

.abonement__description {
  padding: 0;
  margin: 0;
  margin-top: 48px;
  color: $color-default;
  font-size: 24px;
  font-weight: 500;
  line-height: 120%;
  min-width: 419px;

  @include vp-1439 {
    margin-top: 32px;
    font-size: 16px;
    line-height: 24px;
  }
}

.abonement__img {
  position: absolute;
  left: 0;
  top: -100px;

  @include vp-1439 {
    top: -380px;
  }
}

.abonement__button {
  @extend .btn;
  margin: 0;
  margin-top: 48px;
  color: $color-default;
  text-align: right;
  font-size: 24px;
  font-weight: 500;
  line-height: 22px;
  padding: 24px 48px;

  @include vp-1439 {
    margin-top: 32px;
    font-size: 18px;
    padding: 18px 75px;
  }
}
