.exclusive {
  padding-bottom: 1000px;
  background-image: url("../img/exclusive.png");
  background-repeat: no-repeat;
  background-position: 0 50px;
  background-size: cover;
  max-width: 1440px;
  margin:0 auto;
  margin-top: 128px;

  @include vp-1439 {
    margin-top: 72px;
    background-image: none;
    padding-bottom: 0;
  }
}

.exclusive__title {
  margin: 0;
  max-width: 650px;
  color: $color-default;
  font-size: 48px;
  font-weight: 900;
  line-height: 100%;
  letter-spacing: -0.96px;

  @include vp-1439 {
    font-size: 32px;
    line-height: 32px;
  }
}

.exclusive__description {
  position: relative;
  margin: 0;
  margin-top: 48px;
  color: $color-default;
  font-size: 24px;
  font-style: normal;
  font-weight: 500;
  line-height: 120%;
  max-width: 442px;

  @include vp-1439 {
    font-size: 16px;
    line-height: 19px;
  }
}

.exclusive__img {
  @include vp-1439 {
    border-radius: 44px;
  }
}

.exclusive__swiper {
  display: none;
  @include vp-1439 {
    display: block;
    position: relative;
    margin-top: 47px;
  }
}

.exclusive__swiper-decoration {
  position: absolute;
  object-fit: cover;
  z-index: 5;
  top: -74px;
  right: -17px;
}

.swiper-button-next::after {
  background-image: url("../img/svg/arrow-next.svg");
  width: 48px;
  height: 48px;
}

.swiper-button-prev::after {
  background-image: url("../img/svg/arrow-prew.svg");
  width: 48px;
  height: 48px;
}

.exclusive__img-next {
  position: relative !important;
  margin-top: 0 !important;
  width: 48px !important;
  height: 48px !important;
  order: 1;
}

.exclusive__img-prev {
  position: relative !important;
  margin-top: 0 !important;
  width: 48px !important;
  height: 48px !important;
}

.exclusive__img-next::after {
  content: '' !important;
}

.exclusive__img-prev::after {
  content: '' !important;
}

.exclusive__box {
  margin-top: 24px;
  display: flex;
  justify-content: center;
  gap: 36px;
}
