.promo {
  position: relative;
}

.promo__wrapper {
  background-image: url("../img/bg-promo.png");
  background-repeat: no-repeat;
  background-size: contain;
  background-position: 380px 0;
  max-width: 1440px;
  padding: 125px 0 0 0;
  margin: 0 auto;
  width: 100%;

  @include vp-1439 {
    padding-top: 25px;
    padding-bottom: 325px;
    background-position: 0 100%;
    max-width: 375px;
    background-image: url("../img/bg-promo-mobile.png");
  }
}

.promo::after {
  position: absolute;
  background: linear-gradient(180deg, rgba(255, 255, 255, 0.00) 0%, #FFF 100%);
  content: "";
  width: 100%;
  min-height: 100%;
  top: 0;
  left: 0;
  z-index: -2;
}

.promo::before {
  position: absolute;
  background: linear-gradient(0deg, rgba(255, 255, 255, 0.20) 0%, rgba(255, 255, 255, 0.20) 100%), linear-gradient(90deg, #FF5C4D 0%, #EB469F 25%, #8341EF 72%, #3F68F9 100%);
  opacity: 0.22;
  content: "";
  width: 100%;
  min-height: 100%;
  top: 0;
  left: 0;
  z-index: -3;
}

.promo__title {
  margin-top: 72px;
  position: relative;
  color: $color-default;
  font-size: 72px;
  font-weight: 900;
  line-height: 100%;
  letter-spacing: -1.44px;
  max-width: 420px;

  @include vp-1439 {
    font-size: 36px;
    line-height: 36px;
  }
}

.promo__description {
  color: $color-default;
  font-size: 24px;
  font-weight: 500;
  line-height: 130%;
  max-width: 435px;
  margin: 0;
  margin-top: 48px;

  @include vp-1439 {
    font-size: 18px;
    line-height: 23px;

  }
}

.promo__button-box {
  @include vp-1439 {
    position: fixed;
    display: flex;
    align-items: flex-end;
    justify-content: center;
    bottom: 24px;
    left: 0;
    width: 100%;
    height: 49px;
    z-index: 4
  }
}

.promo__button {
  @extend .btn;
  margin-top: 44px;
  text-align: right;
  font-size: 24px;
  font-weight: 500;
  line-height: 22px;

  @include vp-1439 {
    font-size: 18px;
    line-height: 22px;
    padding: 18px 75px;
  }
}

.promo__price {
  position: relative;
  padding: 2px 12px;
  border-radius: 500px;
  background: radial-gradient(141.42% 141.42% at 100% 0%, #E0B8FF 0%, #B6B9FF 100%);
  line-height: 29px;

  @include vp-1439 {
    padding: 0 7px;
    font-size: 18px;
    line-height: 22px;
  }
}

.promo__price::after {
  font-weight: 500;
  position: absolute;
  z-index: -1;
  padding: 6px 12px 7px 27px;
  font-size: 16px;
  line-height: 120%;
  color: #808080;
  top: 0;
  left: 65px;
  content: "3190₽";
  text-decoration: line-through;
  border-radius: 0 500px 500px 0;
  background: linear-gradient(0deg, rgba(255, 255, 255, 0.63) 0%, rgba(255, 255, 255, 0.63) 100%), radial-gradient(141.42% 141.42% at 100% 0%, #E0B8FF 0%, #B6B9FF 100%);

  @include vp-1439 {
    font-size: 13px;
    line-height: 120%;
    top: 0;
    left: 35px;
    padding: 3px 8px 3px 25px;
  }
}
